let baseUrl = 'https://restapi.amap.com'; //
export const request = (options = {}) => {
	//Promise
	return new Promise((resolve, reject) => {
		// 
		uni.request({
			url: baseUrl + options.url || '', //API
			method: options.method || 'GET', //,GET
			data: options.data || {}, //data,
		}).then(data => {
			let [err, res] = data;
			resolve(res);
		}).catch(error => {
			reject(error);
		})
	})
}
