import {
  request
} from './request.js'; //js

//

//
export const getLocation = (data) =>{
  return request({
    url:'/v3/geocode/regeo',
    method:'GET',
    data:data
  })
}

//
export const getServiceList = (data) => {
  return request({
    url: '/v5/place/text',
    method: 'GET',
    data: data
  })
}
// 
export const getDistance = (data) => {
  return request({
    url: '/v3/direction/driving',
    method: 'GET',
    data: data
  })
}

// 
export const getBusDuration = (data) => {
  return request({
    url: '/v3/direction/transit/integrated',
    method: 'GET',
    data: data
  })
}

// 
export const getPlace = (data) => {
  return request({
    url:'/v3/config/district',
    method:'GET',
    data:data
  })
}

// 
export const getServiceDeatil = (data) =>{
  return request({
    url:'/v5/place/detail',
    method:'GET',
    data:data
  })
}